$green: #447b28;
$black: #262525;

html,
body {
  background-color: $black;
}

body {
  margin: 0;
  color: #eee;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 15px;
}

.header {
  background-color: $green;
  height: 60px;
  display: flex;
  align-items: center;
  color: #eee;

  > div {
    padding: 10px 20px;
    padding-right: 5px;

    img {
      height: 40px;
    }
  }
  > div:last-child {
    padding: 10px;
  }
}

.container {
  display: flex;
  flex-flow: row;
  max-width: 800px;
  margin: 0 auto;
  padding: 40px 0;
  opacity: 0;
  margin-top: 0;
  animation: opacityAnim 7s forwards;

  > div:first-child {
    text-align: center;
    overflow: hidden;
    padding: 20px 30px;
    padding-right: 40px;
    padding-bottom: 40px;
    // background-color: #333;
    // border-bottom: solid 2px $green;
  }

  > div:last-child {
    flex: 1;
    text-align: left;
    background-color: #333;
    padding: 0 30px;
    overflow: hidden;
    border-bottom: solid 2px $green;
  }

  > div > div {
    padding-bottom: 15px;
    display: flex;
  }

  > div > div > span:first-child {
    flex: 0 0 20px;
    text-align: center;
    padding-right: 10px;
  }
}

@keyframes opacityAnim {
  0% {
    opacity: 0;
    margin-top: -20px;
  }

  80% {
    opacity: 0;
    margin-top: -20px;
  }

  100% {
    opacity: 1;
    margin-top: 0;
  }
}

a {
  color: lightblue;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

hr {
  margin: 20px 0;
  border-color: #555;
  border-width: 0.5px;
}

h3 {
  font-size: 23px;
  margin: 0 -30px;
  padding: 15px 30px;
  margin-bottom: 25px;
  font-weight: 100;
  font-family: "Nunito", sans-serif;
  background-color: $green;
  display: flex;
  align-items: center;
}

i.fa {
  color: #bbb;
}

#imgContainer {
  position: relative;
  overflow: hidden;
  margin: 0 -30px;
  padding-bottom: 0;
  margin-top: 10px;

  #imgFancy {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    z-index: 1;
    transition: all 0.3s;
  }

  #imgFancy:hover {
    transform: scale(1.25);
  }
}

@media screen and (max-width: 760px) {
  .container {
    flex-flow: column;
  }

  .container > div {
    flex: 1;
  }

  .container > div:first-child {
    margin-right: 0;
  }

  body {
    padding: 0 0;
  }

  h3 {
    text-align: center;
  }
}

#logoContainer {
  z-index: 10;
  position: absolute;
  left: -31px;
  bottom: -33px;
  text-align: center;

  #logoLayer1 {
    border: solid 8px #ffffff;
    padding: 22px;
    display: inline-block;

    transform: scale(0.7) rotateZ(0deg);
    animation: pulse 2s forwards;
    opacity: 1;
  }

  #logoLayer2 {
    width: 50px;
    height: 50px;
    background-color: $green;
  }
}

@keyframes pulse {
  0% {
    opacity: 1;
    transform: scale(0.7) rotateZ(0);
  }

  33% {
    transform: scale(0.5) rotateZ(180deg);
    opacity: 1;
  }

  66% {
    transform: scale(0.7) rotateZ(360deg);
    opacity: 1;
  }

  100% {
    transform: scale(0.3) rotateZ(540deg);
    opacity: 0;
  }
}

#svgLogoAnim {
  position: fixed;
  width: 100%;
  top: 30%;
  left: 30px;
  height: 200px;
  pointer-events: none;
  opacity: 1;
  animation: logoAnim 6s forwards;
}

@keyframes logoAnim {
  0% {
    opacity: 1;
    top: 30%;
  }
  40% {
    opacity: 1;
  }
  80% {
    opacity: 1;
  }
  100% {
    top: 15%;
    opacity: 0;
  }
}

#svgLogoAnim > div#svgImgs {
  width: 140px;
  height: 200px;
  margin: auto;
  position: relative;
}

#svgLogoAnim img {
  height: 200px;
  position: absolute;
  left: 0;
  top: 0;
}

#svgLogoAnim section {
  position: absolute;
  left: 20px;
  bottom: -25px;
  opacity: 0;
  color: #555;
  animation: logoLabel 2s forwards;
  animation-delay: 4s;
  font-family: "Nunito", sans-serif;
}

@keyframes logoLabel {
  0% {
    opacity: 0;
    color: #555;
    left: 20px;
  }
  50% {
    opacity: 0;
    color: #555;
    left: 20px;
  }
  100% {
    opacity: 1;
    color: #fff;
    left: 0;
  }
}

img#box1,
img#box2 {
  animation: imgBox 2.2s forwards;
  opacity: 0;
}

@keyframes imgBox {
  0% {
    opacity: 0;
  }
  66% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

img#box3 {
  clip-path: polygon(0 100%, 100% 100%, 100% 0, 0 0);
  animation: imgBox3 3s forwards;
}

@keyframes imgBox3 {
  0% {
    clip-path: polygon(0 100%, 100% 100%, 100% 100%, 0% 100%);
  }
  60% {
    clip-path: polygon(0 100%, 100% 100%, 100% 100%, 0% 100%);
  }
  100% {
    clip-path: polygon(0 100%, 100% 100%, 100% 0, 0 0);
  }
}

img#petal1 {
  animation: imgPetal1 3s forwards;
  animation-delay: 1s;
  opacity: 0;
  margin-left: -13px;
}

img#petal2 {
  animation: imgPetal1 3s forwards;
  animation-delay: 1.5s;
  opacity: 0;
  margin-left: -13px;
}

img#petal3 {
  animation: imgPetal1 3s forwards;
  animation-delay: 2s;
  opacity: 0;
  margin-left: -13px;
}

@keyframes imgPetal1 {
  0% {
    opacity: 0;
    margin-left: -13px;
  }
  /* 33.3% {
    opacity: 0;
    margin-left: -13px;
  } */
  60% {
    opacity: 0;
    margin-left: -13px;
  }
  100% {
    opacity: 1;
    margin-left: 0;
  }
}
